import PortDetails from '~/web-components/port-details';
import style from './style.scss'


window.addEventListener('DOMContentLoaded', () => {
  let portDetailsList: NodeListOf<PortDetails>
  let y: number
  window.addEventListener('beforeprint', () => {
    y = window.scrollY
    window.scroll({top: 0})
    portDetailsList = document.querySelectorAll('port-details:not([open])')
    for (const portDetails of portDetailsList) portDetails.open = true
  })
  
  window.addEventListener('afterprint', () => {
    for (const portDetails of portDetailsList) portDetails.open = false
    window.scroll({top: y})
  })
}, {once: true})


export { style }
